import { Fragment, useEffect, useState } from "react";
import classnames from "classnames";
import { Button, Divider, Modal, Space } from "antd";
import styles from "./index.module.css";
import dayjs from "dayjs";
import BaseMark from "../BaseMark";
import ItemK from "../ItemK";

const filterIndustry = (list: any) => {
  return list.filter((item: any) => {
    const { list_date, name } = item;

    const is1 = !name.includes("ST");
    const is2 = dayjs().year() - dayjs(list_date).year() >= 4;

    return is1 && is2;
  });
};
const listToTree = (list: Record<string, any>[], level: number) => {
  const tree: any = {};
  list.forEach((item: any) => {
    const { l1_name, l2_name, l3_name } = item;
    if (!tree[l1_name]) {
      tree[l1_name] = {};
    }
    if (level >= 2) {
      if (!tree[l1_name][l2_name]) {
        tree[l1_name][l2_name] = {};
      }
    }
    if (level >= 3) {
      if (!tree[l1_name][l2_name][l3_name]) {
        tree[l1_name][l2_name][l3_name] = [];
      }
    }
    if (level >= 4) {
      tree[l1_name][l2_name][l3_name].push(item);
    }
  });
  return tree;
};
const sortList = (list: any) => {
  list.sort((a: any, b: any) => {
    return (b.roic ?? -999) - (a.roic ?? -999);
  });

  return list;
};

const PageIndustry = (props: any) => {
  const { industry, self, setSelf } = props;

  const [currentStock, setCurrentStock] = useState<any>(null);

  const [tree, setTree] = useState<any>({});
  const [count, setCount] = useState(0);

  const generateData = async () => {
    let d = filterIndustry(industry);
    setCount(d.length);

    d = listToTree(d, 4);
    setTree(d);
  };

  useEffect(() => {
    if (industry.length) {
      generateData();
    }
  }, [industry, self]);

  return (
    <>
      <div className={styles.container}>
        <div className={styles.leftContent}>
          <div className={classnames(styles.wrapIndustry)}>
            {Object.keys(tree).map((k1: string) => {
              if (!tree[k1]) return null;

              return (
                <Fragment key={k1}>
                  <br />
                  <div className="anchor" id={k1} data-name={k1}></div>
                  <Divider orientation="left" style={{ color: "rgb(255 0 0)" }}>
                    {k1}
                  </Divider>
                  {Object.keys(tree[k1]).map((k2) => {
                    return (
                      <Fragment key={k2}>
                        <div className={styles.paddingRight20}>{k2}</div>
                        {Object.keys(tree[k1][k2]).map((k3) => {
                          const k3List = sortList(tree[k1][k2][k3]);
                          const ts_codeString = k3List
                            ?.map((item: any) => item.ts_code)
                            ?.join(",");

                          return (
                            <Fragment key={k3}>
                              <div className={styles.paddingRight40}>{k3}</div>
                              <div className={styles.paddingRight60}>
                                {k3List.map((item: any, index: number) => {
                                  const { name, ts_code, roic } = item;
                                  const { isBuy, tag } = self?.[ts_code] || {};
                                  let tagStyle: any = {};
                                  if (isBuy) {
                                    tagStyle = {
                                      color: "red",
                                      border: "1px solid red",
                                    };
                                  } else if (tag) {
                                    tagStyle = {
                                      border: "1px solid red",
                                    };
                                  }

                                  return (
                                    <span
                                      key={`${ts_code}_${index}`}
                                      className={classnames(styles.tagStyle)}
                                      style={tagStyle}
                                      onClick={() => {
                                        setCurrentStock(item);
                                      }}
                                    >
                                      <span>{name.slice(0, 4)}</span>
                                      <span>
                                        {roic ? ` (${roic.toFixed(1)})` : ""}
                                      </span>
                                    </span>
                                  );
                                })}
                                &nbsp;&nbsp;&nbsp;&nbsp;
                                <span
                                  key="detail"
                                  className={classnames(
                                    styles.gotoBtn,
                                    styles.tagStyle
                                  )}
                                  onClick={() => {
                                    props.setRouter("detail", ts_codeString);
                                  }}
                                >
                                  
                                </span>
                                <span
                                  key="compare"
                                  className={classnames(
                                    styles.gotoBtn,
                                    styles.tagStyle
                                  )}
                                  onClick={() => {
                                    props.setRouter("compare", ts_codeString);
                                  }}
                                >
                                  
                                </span>
                              </div>
                            </Fragment>
                          );
                        })}
                      </Fragment>
                    );
                  })}
                </Fragment>
              );
            })}
          </div>
        </div>
        <div className={styles.rightNav}>
          <Space size="small" direction="vertical">
            <div>Count: {count}</div>
            <Button block type="dashed" onClick={() => props.setRouter("home")}>
              
            </Button>
            {Object.keys(tree)?.map((name: string) => {
              return (
                <Button
                  style={{ color: "#2a5ea7" }}
                  key={name}
                  size="small"
                  type="link"
                  href={`#${name}`}
                >
                  {name}
                </Button>
              );
            })}
          </Space>
        </div>
      </div>
      <Modal
        title={null}
        closable={false}
        footer={null}
        open={currentStock}
        centered
        width="90vw"
        onCancel={() => setCurrentStock(null)}
      >
        <div style={{ width: "100%", height: "90vh", position: "relative" }}>
          <BaseMark data={currentStock} self={self} setSelf={setSelf} />
          <ItemK />
        </div>
      </Modal>
    </>
  );
};

export default PageIndustry;
