import { Checkbox, Select, Spin } from "antd";
import styles from "./index.module.css";
import { useEffect, useState } from "react";
import { selfCodeItem } from "../../Data/useSelf";

const options = [
  { label: 1, value: 1 },
  { label: 2, value: 2 },
  { label: 3, value: 3 },
  { label: 4, value: 4 },
  { label: 5, value: 5 },
  { label: 6, value: 6 },
  { label: 7, value: 7 },
  { label: 8, value: 8 },
  { label: 9, value: 9 },
];

const BaseMark = (props: any) => {
  const { data, self, setSelf } = props;
  const { ts_code, name } = data;

  const [spinning, setSpinning] = useState(false);
  const [isBuy, setIsBuy] = useState(false);
  const [tag, setTag] = useState();

  useEffect(() => {
    if (ts_code) {
      const { isBuy: _isBuy, tag: _tag } = self?.[ts_code] || {};
      setIsBuy(_isBuy);
      setTag(_tag);
    }
  }, [ts_code, self]);

  const handleSelfChange = (e: any) => {
    const ck = e.target.checked;
    modify({
      ts_code,
      name,
      isBuy: ck,
      tag,
    });
  };

  const handleTagChange = (value: number) => {
    modify({
      ts_code,
      name,
      isBuy,
      tag: value,
    });
  };

  const modify = async (newSelf: selfCodeItem) => {
    setSpinning(true);
    await setSelf(newSelf);
    setSpinning(false);
  };

  return (
    <Spin spinning={spinning}>
      <div className={styles.container}>
        <Checkbox checked={isBuy} onChange={handleSelfChange}>
          Self
        </Checkbox>
        <Select
          value={tag}
          onChange={handleTagChange}
          style={{ width: "60px" }}
          listHeight={300}
          options={options}
        ></Select>
      </div>
    </Spin>
  );
};

export default BaseMark;
